<template>
  <footer class="container">
    <div class="row">
      <div class="main">
        <span v-for="(nav, key) in nav_list">
        <a target="_blank" :href="nav.link" v-if="nav.is_http">{{ nav.name }}</a>
        <router-link :to="nav.link" v-else>{{ nav.name }}</router-link>
        <em v-if="key<(nav_list.length-1)"> · </em>
      </span>
        <!--        <a target="_blank" href="">关于荏苒</a>-->
        <!--        <em> · </em>-->
        <!--        <a target="_blank" href="">联系我们</a>-->
        <!--        <em> · </em>-->
        <!--        <a target="_blank" href="">加入我们</a>-->
        <!--        <em> · </em>-->
        <!--        <a target="_blank" href="">帮助中心</a>-->
        <!--        <em> · </em>-->
        <!--        <a target="_blank" href="http://www.jianshu.com/p/cabc8fa39830">合作伙伴</a>-->
        <div class="icp">©2016-2019 广州荏苒信息科技有限公司 / 荏苒 / 粤ICP备16018329号-5 /</div>
      </div>
    </div>
  </footer>
</template>

<script>
export default {
  name: "Footer",
  data() {
    return {
      nav_list: [
        // 提前声明一些默认值,可以在模板渲染的时候防止错误提示
        {
          link: "",
          is_http: "",
          son_list: [],
        }
      ],
    }
  },
  created() {
    this.get_nav();
  },
  methods: {
    get_nav() {
      // 获取导航信息
      this.$axios.get(`${this.$settings.Host}/nav/footer/`).then(response => {
        this.nav_list = response.data;
      }).catch(error => {
        this.$message.error("网络异常,无法获取导航信息!");
      });
    }
  }
}
</script>

<style scoped>
.container {
  width: 960px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 20px;
  box-sizing: border-box;
}

.container:after, .container:before {
  content: " ";
  display: table;
}

footer .row {
  padding-top: 25px;
  box-sizing: border-box;
  margin-left: -15px;
  margin-right: -15px;
}

footer .main {
  padding-right: 0;
  font-size: 13px;
  color: #969696;
  width: 70.83333%;
}

footer .icp, footer .icp a {
  color: #c8c8c8;
}

footer .icp {
  margin-top: 10px;
  font-size: 12px;
}

footer .main a {
  color: #969696;
  display: inline-block;
}

.row:after {
  clear: both;
}
</style>



